html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  color: #2b2d42;
  background-image: linear-gradient(to bottom, #ffffff 55%, #56cbf9 55%);
}
@media (min-width: 768px) {
  body {
    background-image: linear-gradient(to right, #ffffff 50%, #56cbf9 50%);
  }
}

img {
  display: block;
  max-width: 100%;
  width: auto;
  height: auto;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

[class$=__container] {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

h1 {
  margin: 0;
  line-height: 1.3;
}

h2 {
  margin: 0;
  line-height: 1.3;
}

h3 {
  margin: 0;
  line-height: 1.3;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

[class$=__heading] {
  margin: 0;
}

a {
  text-decoration: none;
  color: #2b2d42;
}

p {
  font-size: 1.8rem;
  line-height: 1.5;
  margin: 0.5rem 0 1.5rem;
}

.header {
  padding: 2rem 0;
}
.header__logo {
  display: block;
  border: 0.3rem solid #56cbf9;
  border-radius: 50%;
  padding: 0.5rem;
  width: 6rem;
}

.main {
  padding: 2rem 0;
}
.main__grid {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  gap: 3rem;
}
@media (min-width: 768px) {
  .main__grid {
    grid-template-rows: unset;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10rem;
  }
}

.patient-tracking {
  padding: 1rem 0;
}
.patient-tracking__heading {
  color: #2b2d42;
  font-weight: 700;
}
.patient-tracking__description {
  font-weight: 500;
}
.patient-tracking__description--highlight {
  color: #56cbf9;
}

.patient-management {
  padding: 1rem 0;
}
.patient-management__heading {
  color: #ffffff;
  font-weight: 700;
}
.patient-management__description {
  font-weight: 500;
  color: #ffffff;
}
.patient-management__description--highlight {
  color: #2b2d42;
}

.appointment-list {
  display: grid;
  gap: 1rem;
}

.form {
  display: grid;
  gap: 1rem;
}
.form__field {
  display: flex;
  flex-direction: column;
}
.form__label {
  margin: 0 0 0.5rem 1rem;
  font-weight: 500;
  font-size: 1.6rem;
}
.form__input {
  width: 100%;
  outline: none;
  border: 0.1rem solid #2b2d42;
  border-radius: 1rem;
  padding: 0.5rem;
  font-size: 1.8rem;
  font-weight: 400;
  transition: border-color 0.2s ease-in-out;
}
.form__input:focus {
  border-color: #56cbf9;
}
.form__input--textarea {
  resize: vertical;
  min-height: 10rem;
}
.form__btn {
  margin-top: 2rem;
  display: block;
  padding: 1rem 2rem;
  background-color: #2b2d42;
  font-size: 2rem;
  font-weight: 700;
  color: #ffffff;
  text-align: center;
  text-decoration: none;
  border-radius: 1rem;
  border: none;
}
@media (min-width: 768px) {
  .form__btn {
    width: auto;
  }
}
@media (hover: hover) {
  .form__btn {
    transition: background-color 0.3s ease-in-out;
  }
  .form__btn:hover {
    background-color: rgb(73.1788990826, 76.5825688073, 112.3211009174);
    cursor: pointer;
  }
}

.appointment-list__item {
  border-radius: 1rem;
  padding: 1rem;
  background-color: #ffffff;
}
.appointment-list__item-info {
  display: flex;
  flex-wrap: wrap;
  column-gap: 1rem;
  margin: 0;
}
.appointment-list__item-detail {
  text-transform: uppercase;
  font-weight: 700;
  margin: 0;
}
.appointment-list__item-detail--value {
  font-weight: 300;
  text-transform: none;
}